.Menu_sidebar__1775C {
  display: block;
  height: 100%; /* Altezza massima a schermo intero */
  width: 160px; /* Larghezza della barra laterale */
  position: fixed; /* Fissare la barra laterale */
  z-index: 1; /* Posizionare la barra laterale sopra il contenuto */
  top: 0; /* Allineamento in alto */
  right: 0; /* Allineamento dex */
  background-color: #222222d5; /* Colore di sfondo */
  overflow-x: hidden; /* Nascondere il contenuto che va oltre la barra laterale */
  padding-top: 20px; /* Spazio extra in cima alla barra laterale */
  z-index: 1000;
}
  
.Menu_sidebar__1775C a, .Menu_deskMenu__ZreqP a {
  display: flex; /* Converte il link in un blocco */
  color: #f1f1f1; /* Colorazione del testo */
  padding: 16px; /* Aggiungere spazio intorno ai link */
  text-decoration: none; /* Rimuovere la sottolineatura dei link */
  
}

.Menu_sidebar__1775C a:hover{
  background-color: #F0AD0D;

 /*  background-color: #ddd; /* Cambia il colore di sfondo del link al passaggio del mouse */ 
  color: black; /* Cambia il colore del testo del link al passaggio del mouse */
}
